@import url('https://fonts.googleapis.com/css2?family=Handlee&family=Open+Sans&family=Princess+Sofia&display=swap');

$text: black ;

/* Main colors */
$mh: hsl(120deg, 100%, 50%) ; // Main hue
$ch: adjust-hue($mh, 60deg) ; // Cooler hue
$wh: adjust-hue($mh, -60deg) ; // Warmer hue


/* Main hue gradients */
$mg50: desaturate($mh, 75%) ; // Sets overall saturation

/* Main hue shades */
$mg45: darken($mg50, 05%) ;
$mg40: darken($mg50, 10%) ;
$mg35: darken($mg50, 15%) ;
$mg30: darken($mg50, 20%) ;
$mg25: darken($mg50, 25%) ;
$mg20: darken($mg50, 30%) ;
$mg15: darken($mg50, 35%) ;
$mg10: darken($mg50, 40%) ;
$mg05: darken($mg50, 45%) ;

/* Main hue tints */
$mg95: lighten($mg50, 45%) ;
$mg90: lighten($mg50, 40%) ;
$mg85: lighten($mg50, 35%) ;
$mg80: lighten($mg50, 30%) ;
$mg75: lighten($mg50, 25%) ;
$mg70: lighten($mg50, 20%) ;
$mg65: lighten($mg50, 15%) ;
$mg60: lighten($mg50, 10%) ;
$mg55: lighten($mg50, 5%) ;


/* Cooler hue gradients */
$cg50: desaturate($ch, 75%) ; // Sets overall saturation

/* Cooler hue shades */
$cg45: darken($cg50, 05%) ;
$cg40: darken($cg50, 10%) ;
$cg35: darken($cg50, 15%) ;
$cg30: darken($cg50, 20%) ;
$cg25: darken($cg50, 25%) ;
$cg20: darken($cg50, 30%) ;
$cg15: darken($cg50, 35%) ;
$cg10: darken($cg50, 40%) ;
$cg05: darken($cg50, 45%) ;

/* Cooler hue tints */
$cg95: lighten($cg50, 45%) ;
$cg90: lighten($cg50, 40%) ;
$cg85: lighten($cg50, 35%) ;
$cg80: lighten($cg50, 30%) ;
$cg75: lighten($cg50, 25%) ;
$cg70: lighten($cg50, 20%) ;
$cg65: lighten($cg50, 15%) ;
$cg60: lighten($cg50, 10%) ;
$cg55: lighten($cg50, 5%) ;


/* Warmer hue gradients */
$wg50: desaturate($wh, 75%) ; // Sets overall saturation

/* Warmer hue shades */
$wg45: darken($wg50, 05%) ;
$wg40: darken($wg50, 10%) ;
$wg35: darken($wg50, 15%) ;
$wg30: darken($wg50, 20%) ;
$wg25: darken($wg50, 25%) ;
$wg20: darken($wg50, 30%) ;
$wg15: darken($wg50, 35%) ;
$wg10: darken($wg50, 40%) ;
$wg05: darken($wg50, 45%) ;

/* Warmer hue tints */
$wg95: lighten($wg50, 45%) ;
$wg90: lighten($wg50, 40%) ;
$wg85: lighten($wg50, 35%) ;
$wg80: lighten($wg50, 30%) ;
$wg75: lighten($wg50, 25%) ;
$wg70: lighten($wg50, 20%) ;
$wg65: lighten($wg50, 15%) ;
$wg60: lighten($wg50, 10%) ;
$wg55: lighten($wg50, 5%) ;


$shade: transparentize(black, 0.75) ;
$gloss: transparentize(white, 0.25) ;


html {
	height: 100% ;
	background: black ;
	color: $text ;
	font: {
		family: 'Open Sans', sans-serif ;
		size: 1pc ;
	}
	line-height: 1 ;
}

body {
	display: flex ;
	flex-flow: column nowrap ;
	justify-content: space-between ;
	min-height: 100vh ;
	padding: 0 ;
	margin: 0 ;
}

section, header, footer, main {	display: block ; }

a {
	border: 0 ;
	color: $text ;
	text-decoration: underline ;
	cursor: pointer ;
	transition: color 0.5s ; 

	&:link,
	&:visited {
		color: $mg30 ;
	}
	&:hover { color: $mg35 ; }
	&:active { color: $mg40 ; }

	img { border: 0 ; }
}

code {white-space: normal ; }

h1,h2,h3,h4,h5,h6 {
	margin: 0 auto ;
	padding: 1rem ;
	border: none ;
	outline: none ;
	color: $mg30 ;
	font: {
		family: Handlee, 'Open Sans', sans-serif ;
		weight: normal ;
	}
	text-align: center ;
	line-height: 1.25 ;
	text-shadow: 0.0625em 0.0625em 0.0625em $shade ;

	span { white-space: nowrap ; } 

}

h1 { font-size: calc(1.5rem + 1.2vmin) ; }
h2 { font-size: calc(1.4rem + 1.0vmin) ; }
h3 { font-size: calc(1.3rem + 0.8vmin) ; }
h4 { font-size: calc(1.2rem + 0.6vmin) ; }
h5 { font-size: calc(1.1rem + 0.4vmin) ; }
h6 { font-size: calc(1.0rem + 0.2vmin) ; }

.toggleArrow {
	opacity: 0.25 ;
	transition: transform 0.5s ;

	&.left {
		float:left ;
		&.collapsed { transform: rotate(-90deg) ; }
		}
	&.right {
		float:right ;
		&.collapsed { transform: rotate(90deg) ; }
	}
}


header {
	padding: 0 ;
	border: 0 ;
	background: black ; 
	color: $text ;
	text-align: center ;

	h1 {
		background: black url(/images/banner.jpg) center top no-repeat;
		margin: 0 auto ;
		padding: 1rem ;
		padding-top: 144px ;
		border: 0 ;
		color: $mg60 ;
		font-family: "Princess Sofia", 'Open Sans', sans-serif;
		letter-spacing: 0.1em ;
		line-height:1.25;
	}
	a { text-decoration:none ; }

}


footer {
	height: 144px;
	margin: 0 ;
	padding: 0 ;
	border: 0 ;
	outline: 0 ;
	background: black url(/images/footer.jpg) center bottom no-repeat ;
	color: $cg50 ;
	text-align: center ;

	p {
		margin: 0 ;
		padding: 1em ;
	}
	a {
		&:link { color: $cg70 ; }
		&:hover { color: $cg80 ; }
		&:active { color: $cg90 ; }
		&:visited { color: $cg70 ; }
	}
}

#menu {
	position: sticky ;
	top: 0 ;
	z-index: 1 ;
	max-height: 100vh ;
	min-height: 4em ;
	overflow-x: hidden ;
	overflow-y: auto ;
	transition: height 1s ;
	padding: 0 ;
	background: linear-gradient(black 0em, $cg05 2em, $cg25 2em, $cg10 4em, $cg25 4em, $cg15 6em, $cg25 6em, $cg20 8em, $cg25 8em) no-repeat, black ;
	background-position-y: 4em ;
	background-attachment: local ;
	
	@media (min-width: 40rem ) {
		max-height: auto ;
		min-height: auto ;
		overflow-x: visible ;
		overflow-y: visible ;
		background-position-y: 0 ;
	}

	#hamburger {
		clear: both ;
		width: 100vw;
		margin: 0 ;
		padding: 0 ;
		border: 0 ;
		border-top: 1pt $cg10 solid;
		background: black ;
		text-align: center ;
		line-height: 2 ;

		@media (min-width: 40rem) { display: none ; }

		a {
			float: none ;
			display: block ;
			width: 100% ;
			margin: 0 ;
			padding: 1rem 0 ;
			border: none;
			border-radius: 0 ;
			background: none ;
			color: #FFF ;
			text-decoration: none ;
			box-shadow: none ;

			&:hover, &:active {
				background: none ;
			}
		}

		svg {
			fill: #355 ;
			vertical-align: middle ;
		}

	}

	#fries {
		display: none ;
		@media (min-width: 40rem) { display: block ; }
	}

	a {
		display: block ;
		float: left ;
		margin: 0 ;
		margin-left: 0.5em ;
		padding: 0 1em ;
		border:none;
		outline:none;
		border-top-left-radius: 0.5em ;
		border-top-right-radius: 0.5em ;
		background: linear-gradient(to bottom, $cg45, $cg25) ;
		color: black ;
		text: {
			align: center ;
			decoration: none ;
		}
		line-height: 2 ;
		box-shadow: 1pt 1pt 0pt $gloss inset, -1pt 0pt 0pt $shade inset ;

		&:hover { background: linear-gradient(to bottom, $cg65, $cg25) ; }
		&:visited { color: black ; }

		&.current {
			position: relative ;
			border: 1pt outset $cg70;
			border-bottom: none ;
			background: linear-gradient(to bottom, $cg90, $cg85) ;
			text-decoration: none ;

			&:hover {
				background: white linear-gradient(to bottom, white, $cg85) ;
			}
		}
	}

	ul {
		display: flex ;
		flex-flow: row wrap;
		justify-content: space-between;
		clear: both ;
		margin: 0 ;
		padding: 0.25em;
		border: 0 ;
		background: linear-gradient(to bottom, $cg85, $cg75) ;
		text-align: center ;
		line-height: 2.5;
		box-shadow: 0pt -1pt 0pt $shade inset, 0pt 1pt 1pt $shade;

		> * {
			flex: 1 1 100% ;
			white-space: normal ;
			@media (min-width: 40rem) {
				flex: 1 0 10em ;
				white-space: nowrap ;
			}
		}

		li {
			position:relative;
			display: inline-block ;
			height:auto;
			margin: 0.25em;
			padding: 0 ;
			border-radius: 0.5em ;
			line-height: 1 ;
			list-style-type: none ;

			a {
				display: inline-block;
				float: none ;
				width: calc(100% - 4em) ;
				margin: 0 ;
				padding: 0.5em 2em ;
				border: none ;
				border-radius: 0.5em ; 
				background:
					radial-gradient(transparentize($ch, 1), transparentize($ch, 0.9)),
					linear-gradient(to bottom, $cg95, $cg75) ;
				color: black ;
				font-weight: normal ;
				text: {
					transform: none ;
					decoration: none ;
				}
				line-height: 1 ;
				box-shadow: 1pt 1pt 0pt $gloss inset, -1pt -1pt 0pt $shade inset, 1pt 1pt 1pt $shade;
				
				&:hover {
					background:
						radial-gradient(transparentize($mh, 1), transparentize($mh, 0.9)),
						linear-gradient(to bottom, $mg95, $mg75) ;
				}
				&:active {
					background:
						radial-gradient(transparentize($wh, 1), transparentize($wh, 0.9)),
						linear-gradient(to bottom, $wg95, $wg75) ;
				}
				
				/* For the dropdown menu arrows */
				span {
					display: inline-block ;
					position: absolute ;
					top: 0.5em ;
					opacity: 0.25 ;
					vertical-align: middle ;
					transition: transform 0.5s ;
					transform: rotate(0deg) ;
					
					&:first-of-type {
						left: 0.5em ;
						margin-right: 0.5em ;
						transform: rotate(-90deg) ;
					}
					&:last-of-type {
						right: 0.5em ;
						margin-left: 0.5em ;
						transform: rotate(90deg) ;
					}
				}
			}

			/* An active dropdown menu button */
			&.current {
				position:relative;
				z-index:1;
				
				a {
					background:
						radial-gradient(transparentize($mh, 1), transparentize($mh, 0.9)),
						linear-gradient(to bottom, $mg95, $mg75) ;
					&:hover {
						background:
							radial-gradient(transparentize($wh, 1), transparentize($wh, 0.9)),
							linear-gradient(to bottom, $wg95, $wg75) ;
					}
					&:active {
						background:
							radial-gradient(transparentize($wh, 1), transparentize($wh, 0.9)),
							linear-gradient(to bottom, $cg95, $cg75) ;
					}
					
					/* Flip the dropdown menu arrows */
					span:first-of-type, span:last-of-type {
						transform: rotate(0) ; 
					}
				}
			}

			/* The contents of the dropdown menu */
			ul {
				display :none ;
				overflow-y: auto ;
				position: static ;
				z-index: -1 ;
				top: 0 ;
				left: 0.5em ;
				width: 100% ;
				padding: 0.125em 0 ;
				border-radius: 0.5em ;
				background: linear-gradient(to right, $mg75, $mg90, $mg75) ;
				box-shadow: 1pt 1pt 0pt $gloss inset, -1pt -1pt 0pt $shade inset, 1pt 1pt 1pt $shade ;

				@media (min-width: 40rem) {
					position: absolute ;
					width: calc(100% - 1em) ;
					max-height: calc(100vh - 4.5em) ;
					padding: 0 ;
					padding-top: 2em ;
				}

				li {
					display: block ;
					margin: 0.5em ;
					white-space: normal ;

					a {
						width: calc(100% - 2em) ;
						padding: 0.5em 1em ;
					}
				}
			}
		}
	}
}


main {
	display: flex ;
	flex-flow: column nowrap ;
	justify-content: center ;
	flex: 1 1 auto ;
	margin: 0 ;
	padding: 1em ;
	border: 0 ;
	outline: 0 ;
	background: radial-gradient(farthest-corner, $wg90, $wg95) ;
	line-height: 1.5 ;

	> * {flex: 1 1 auto ; }

	p {
		text-indent: 2em ;

		&.description {
			padding: 1em ;
			padding-top: 0 ;
			border-radius: 1em ;
			border: none ;
			border-bottom: 1pt groove $wg75 ;
			font-style: italic ;
			text: {
				align: center ;
				indent: 0 ;
			}
		}
	}

	ul, ol {
		margin: 0 ;
		padding: 0 ;

		li {
			margin: 1em 0 ;
			padding: 0 ;
			padding-left: 2em ;
			list-style-type: none ;

			&:before {
				content: "•" ;
				float: left ;
				width: 1em ;
				margin: 0 ;
				margin-left: -1em ;
			}
		}

		p {
			border: 0 ;
			text-indent: 0 ;
		}

		.toc {
			text-align: center ;

			li {
				margin: 0 ;
				margin-bottom: 0.5em ;
				padding: 0 ;
				line-height: 1.25 ;

				&:before { display: none ; }
				span { white-space: nowrap ; }
			}
		}
	}

	blockquote {
		position: relative ;
		margin: 0 auto ;
		padding: 1em 2em ;
		border: 1pt dashed $wg75 ;
		border-radius: 0.5em ;
		
		&:before {
			content: "“" ;
			display: block ;
			width: 1em ;
			height: 1em ;
			position: absolute ;
			top: 0 ;
			left: 1rem ;
			font-size: 2em ;
			text-align: left ;
		}
		&:after {
			content:"”";
			display: block ;
			width: 1em ;
			height: 1em ;
			position: absolute ;
			bottom: 0 ;
			right: 1rem ;
			font-size: 2em ;
			text-align: right ;
		}
	}

	hr {
		content: url(/images/hr-vine.svg) ;
		clear: both ;
		width: 100% ;
		margin: 1em auto ;
		padding: 0 ;
		border: 0 ;
		outline: 0 ;
		filter: drop-shadow(0 2pt 1pt $shade) ;

		&:nth-of-type(even) {
			transform: scaleX(-1) ; 
		}
	}

	table {
		width: calc(100% - 2em) ;
		font-size: 1em ;
		border: 1pt solid black ;
		margin: 1em auto auto auto ;
		background: #CCC ;
		th {
			padding: 1em ;
			border: 1pt solid black ;
			background: $mg95 ;
			text-align: center ;
			td {
				padding: 1em ;
				border: 1pt solid black ;
				background: white ;
				text-align: center ;
			}
		}
	}

	section {
		clear: both ;
		width: calc(100% - 2em) ;
		max-width: 80em ;
		margin: 0 auto;
		margin-bottom: 1em ;
		padding: 1em ;
		border: 0 ;
		outline: 0 ;
		border-radius: 2em;
		background: radial-gradient(farthest-corner, transparentize(white,0.875), transparentize(white,0.5)) ;
		box-shadow: 2pt 2pt 2pt $shade, -2pt -2pt 2pt $gloss, 1pt 1pt 0pt $gloss inset, -1pt -1pt 0pt $shade inset ;

		&:after {
			content: "" ;
			display: block ;
			height: 0 ;
			clear: both ;
		}

		section {
			border-radius: 1.75em ;
			section {
				border-radius: 1.5em ;
				section {
					border-radius: 1.25em ;
					section {
						border-radius: 1em ;
					}
				}
			}
		}

		.duo {
			display: flex ;
			flex-flow: row wrap ;
			justify-content: space-around ;
			align-items: flex-start ;
			text-align: center ;

			> * {
				display: inline-block ;
				float: none ;
				flex: 1 1 20em ;
				width: 36em ;
				max-width: calc(100% - 2em) ;
				margin: 1em 1em ;
				padding: 0 1em ;
				vertical-align: top ;
			}
			section { text-align: left ; }
			figure li {
				margin: 0.125em 0 ;
				&:before {
					content: none ;
				}
			}
		}

		figure {
			float: none ;
			display: block ;
			width: 100% ;
			max-width: 100% ;
			margin: 1em auto ;
			transition: max-width 1s ease-in 0.25s ;

			img {
				display: block ;
				max-width: 100% ;
				margin: auto ;
			}

			ul {
				margin: 0 ;
				padding: 0 ;
				list-style-type: none ;
			}

		}

		@media (min-width: 40rem) {
			figure {
				float: left ;
				max-width: 25em ;
				margin: 0 1em 1em 0 ;

				&:nth-of-type(even) {
					float:right;
					margin-left:1em;
					margin-right:0;
				}

				&:hover {
					max-width:100%;
				}
			}
			&:nth-of-type(even) figure {
				float:right;
				margin-left:1em;
				margin-right:0;

				&:nth-of-type(even) {
					float:left;
					margin-right:1em;
					margin-left:0;
				}
			}
			.solo {
				float: none ;
				max-width: 100% ;

				picture, img {
					max-width: 100% ;
				}
			}
		}
	}

	.gallery {
		display:flex ;
		flex-flow: row wrap ;
		justify-content: space-between ;
		align-items: stretch ;
		max-width:100% ;
		margin: 0 auto ;
		margin-bottom: 1em ;
		padding:1em;
		border: 0 ;
		outline: 0 solid #000 ;
		border-radius: 2em;
		background: radial-gradient(farthest-corner, transparentize(white,0.875), transparentize(white,0.5)) ;
		text-align: center ;
		box-shadow: 2pt 2pt 2pt #DDC, -2pt -2pt 2pt #EED, 1pt 1pt 0pt rgba(255,255,255,0.75) inset, -1pt -1pt 0pt rgba(0,0,0,0.25) inset ;

		> * {
			flex: 2 0 16em ;
		} 

		li {
			display: flex ;
			flex-flow: column nowrap ;
			justify-content: space-between ;
			width: 16em ;
			margin: 0.5em ;
			padding: 0 1em ;
			border: 0 ;
			border-radius: 1em;
			background-color: white ;
			text-align: center ;
			list-style: none;
			vertical-align: top ;
			box-shadow: 2pt 2pt 1pt $wg90, -2pt -2pt 2pt $wg95, 1pt 1pt 0pt $gloss inset, -1pt -1pt 0pt $shade inset ;

			&:before { display: none ; }

			h3 {
				margin: 0 ;
				padding: 0.5em 2em ;
				font: {
					family: 'Open Sans', sans-serif ;
					size: 1rem ;
				}
				text-transform: none ;
				line-height: 1.5 ;
			}

			img {
				display: inline-block ;
				max-width: 16em ;
				max-height: 16em ;
				margin: 0 auto ; 
				background: white ;
			}

			p, p.description {
				margin: 0 auto ;
				padding: 0.5em 2em ;
				border: 0 ;
				text: {
					indent: 0 ;
					align: center ;
				}
				line-height: 1.5 ;
			}
		}
	}

	.display {
		display: flex ;
		flex-flow: column nowrap ;
		float: none ;
		max-width: 100% ;
		height: 100% ;
		margin: -1em ;
		padding: 2em ;
		background: black ;
		color: white ;

		img {
			max-width: 100% ;
			max-height: calc(75vh - 288px) ;
			margin:0 auto ;
			object-fit: scale-down ;
			background: white ;
		}

		h2 {
			color: grey ;
		}
	}
}


/* Modal image display */
.modal {
	position: fixed ;
	z-index: 3 ;
	top: 0 ;
	left: 0 ;
	right: 0 ;
	bottom: 0 ;
	display: flex ;
	flex-flow: column nowrap ;
	justify-content: center ;
	align-items: center ;
	overflow: hidden ;
	margin: 0 ;
	padding: 0 ;
	opacity: 0 ;
	background: radial-gradient(black,transparentize(black,0.0625)) ;
	color: grey ;
	font-size: calc(1rem + 1vmin) ;
	text-align: center ;

	img {
		position: absolute ;
		top: 50% ;
		left: 50% ;
		max-width: calc(100vw - 2 * (2vh + 1em)) ;
		max-height: calc(100vh - 2 * (2vh + 5em)) ;
		object-fit: scale-down;
		transform: translate(-50%, -50%) ;
		border: 1vh solid white ;
		box-shadow: 0 0 3em 3em black ;
	}

	h2, nav {
		display: inline ;
		position: absolute ;
		z-index: 4 ;
		top: 0 ;
		width: auto ;
		padding: 2em ;
		color: grey ;
	}

	nav {
		top: auto ;
		bottom: 0 ;
		background: none ;
	}

	a {
		display: inline-block ;
		width: 1em ;
		margin: 0 1em ;
		padding: 0 ;
		border-radius: 1em ;
		background: none ;
		color: grey ;
		font-weight: bold ;
		text-decoration: none ;
		text-shadow: 0 0 0.125em black ;
		user-select: none ;
		cursor: pointer ;

		&:hover, &:focus {
			color: white;
		}
	}

}